import axios from "axios";
import React, { useContext, useEffect, useRef, useState } from "react";
import { ModalContext } from "./Context/ModalProvider";
import Use from "./Use";
import Donate from "./Donate";
import * as XLSX from "xlsx";
//import LoginComponent from "./LoginComponent";

function App() {
 // const loggedInUser = localStorage.getItem('qrtrasau');
  const [timeNow, setTimeNow] = useState(" ");
  const [percent, setpercent] = useState(0.2);
  const copyRef = useRef(null);
  const [loading, setloading] = useState(false);
  const { setisShow, setContent } = useContext(ModalContext);
  const [statusQR, setstatusQR] = useState("All");
  const [phoneNumbers, setPhoneNumbers] = useState([]);
  const [passwordT, setPasswordT] = useState("");
  const [password, setPassword] = useState("");
  const [newPhoneNumbers, setNewPhoneNumbers] = useState("");
  const [AllAmount, setAllAmount] = useState([]); // lu All data api tra v
  const [DataHandle, setDataHandle] = useState(AllAmount);
  const [totalAmount, setTotalAmount] = useState(0);
  const [ActiveDay, setActiveDay] = useState("Now");
  const today = new Date();
  const [day, setDay] = useState(String(today.getDate()).padStart(2, "0"));
  const [month, setMonth] = useState(
    String(today.getMonth() + 1).padStart(2, "0")
  );
  const [year, setYear] = useState(today.getFullYear());
  const [error, seteError] = useState();
  const formattedDate = `${year}-${month}-${day}`;
  const loginAndGetAmount = async (username) => {
    const getmerchant =
      "https://vannghimomo.onrender.com/api/profile/v2/merchants?language=vi";

    const data = {
      username: username,
      password: password,
    };

    try {
      const response = await axios.post(
        "https://vannghimomo.onrender.com/api/authentication/login?language=vi",
        data
      );
      const token = response.data.data.token;
      const merchantResponse = await axios.get(getmerchant, {
        headers: {
          Authorization: "Bearer " + token,
        },
      });
      const merchantId = merchantResponse.data.data.merchantResponseList[0].id;
      const brandName =
        merchantResponse.data.data.merchantResponseList[0].brandName;
      const transactionData = await axios.get(
        `https://vannghimomo.onrender.com/api/transaction/v2/transactions/statistics?fromDate=${
          ActiveDay === "AllDay"
            ? firstDay
            : ActiveDay === "AllDayThangTruoc"
            ? firstDayx
            // : ActiveDay==="AllDaysSelect"
            // ? startDate
            : formattedDate
        }T00%3A00%3A00.00&toDate=${
          ActiveDay === "AllDay"
            ? lastDay
            : ActiveDay === "AllDayThangTruoc"
            ? lastDayx
            : formattedDate
        }T23%3A59%3A59.00&status=ALL&merchantId=${merchantId}&language=vi`,
        {
          headers: {
            Authorization: "Bearer " + token,
          },
        }
      );

      const statusPaylayter = isPhoneNumber(phoneNumbers[0])
        ? await axios.get(
            `https://vannghimomo.onrender.com/api/profile/v2/merchants/${merchantId}/paylater?language=vi`,
            {
              headers: {
                Authorization: "Bearer " + token,
              },
            }
          )
        : 0;

      const enabled = statusPaylayter?.data?.data?.enabled;
      const locked = statusPaylayter?.data?.data?.locked;
      const useMoneyPaylater = statusPaylayter?.data?.data?.used;
      const totalSuccessAmount = transactionData?.data?.data?.totalSuccessAmount || 0;
      const totalPendingAmount = transactionData?.data?.data?.totalPendingAmount || 0;
      return {
        amount: totalSuccessAmount,
        brandName,
        enabled,
        locked,
        useMoneyPaylater,
        pendingAmount: totalPendingAmount
      };
    } catch (error) {
      seteError(
        " C Li, Cn Chy Li... Kim Tra Cc ST phi cng 1 Mt Khu Nh. Thc Hin Li Sau 5s"
      );
      setloading(false);
      //console.error("Li Nghi i:", error);
      return { amount: 0, brandName: "Trng",pendingAmount:0};
    }
  };
  const loginAllPhones = async () => {
    setAllAmount([]);
    setTotalAmount(0);
    seteError();
    setTimeNow("");
    setloading(true);
    for (const phone of phoneNumbers) {
      const { amount, brandName,enabled, locked, useMoneyPaylater, pendingAmount } =
        await loginAndGetAmount(phone);
      setAllAmount((prevAmounts) => [
        ...prevAmounts,
        { phone, amount, brandName,enabled, locked, useMoneyPaylater,pendingAmount  },
      ]);
      setTotalAmount((prevTotal) => prevTotal + amount);
    }
    setloading(false);
    setTimeNow(`${today.getHours()}:${today.getMinutes()}`);
  };
  const handleAddPhoneNumbers = () => {
    const phonesArray = newPhoneNumbers
      .split(/[,\s]+/)
      .filter((phone) => phone.trim() !== "");
    setPhoneNumbers([...phoneNumbers, ...phonesArray]);
    setPassword(passwordT);
    setPasswordT("");
    setNewPhoneNumbers("");
  };

  const HandleTotalYesterday = () => {
    const yesterday = new Date(today);
    yesterday.setDate(today.getDate() - 1); // Li v ngy hm qua

    // Cp nht trng thi vi ngy, thng v nm ca ngy hm qua
    setActiveDay("Yesterday");
    setDay(String(yesterday.getDate()).padStart(2, "0"));
    setMonth(String(yesterday.getMonth() + 1).padStart(2, "0"));
    setYear(yesterday.getFullYear());
  };
  const getYesterdayDate = () => {
    const yesterday = new Date(today);
    yesterday.setDate(today.getDate() - 1);

    const dayYesterday = String(yesterday.getDate()).padStart(2, "0");
    const monthYesterday = String(yesterday.getMonth() + 1).padStart(2, "0");

    return { dayYesterday, monthYesterday };
  };

  const { dayYesterday, monthYesterday } = getYesterdayDate();
  const HandleTotalNow = () => {
    setActiveDay("Now");
    setDay(String(today.getDate()).padStart(2, "0"));
    setMonth(String(today.getMonth() + 1).padStart(2, "0"));
  };
  const HandleTotalAllDayMonth = () => {
    setActiveDay("AllDay");
  };
  const HandleTotalAllDayMonthThangTruoc = () => {
    setActiveDay("AllDayThangTruoc");
  };
  function formatDate(date) {
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, "0"); // Thng bt u t 0
    const day = String(date.getDate()).padStart(2, "0");
    return `${year}-${month}-${day}`;
  }
  function getFirstAndLastDayOfMonth(year, month) {
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);
    return { firstDay: formatDate(firstDay), lastDay: formatDate(lastDay) };
  }

  const { firstDay, lastDay } = getFirstAndLastDayOfMonth(
    today.getFullYear(),
    String(today.getMonth() + 1).padStart(2, "0") - 1
  );
  // thng trc
  function getFirstAndLastDayOfMonthx(year, month) {
    const lastMonth = month - 1;
    // Nu thng l thng Ging (0), nm s gim i 1 v thng s l thng 12 (11)
    const adjustedYear = lastMonth < 0 ? year - 1 : year;
    const adjustedMonth = (lastMonth + 12) % 12;
    const firstDay = new Date(adjustedYear, adjustedMonth, 1);
    const lastDay = new Date(adjustedYear, adjustedMonth + 1, 0);
    return {
      firstDayx: formatDate(firstDay),
      lastDayx: formatDate(lastDay),
    };
  }
  const { firstDayx, lastDayx } = getFirstAndLastDayOfMonthx(
    today.getFullYear(),
    String(today.getMonth() + 1).padStart(2, "0") - 1
  );
  const QrHasNotBeenScanned = () => {
    setDataHandle(AllAmount.filter((item) => item.amount === 0));
    setstatusQR("notscan");
  };
  const QrPendingMoney = ()=>{
    setDataHandle(AllAmount.filter((item)=>item.pendingAmount > 0));
    setstatusQR("pendingMoney");
  }
  const QrHasBeenScanned = () => {
    setDataHandle(AllAmount.filter((item) => item.amount > 0));
    setstatusQR("scan");
  };
  const QrHasBeenScannedAll = () => {
    setDataHandle(AllAmount);
    setstatusQR("All");
  };
  const ClearData = () => {
    window.location.reload();
  };
  const HandleUse = () => {
    setisShow(true);
    setContent(<Use />);
  };
  useEffect(() => {
    setDataHandle(AllAmount);
  }, [AllAmount]);

  // Kim tra xem chui c ch ton s
  function isPhoneNumber(str) {
    return /^\d+$/.test(str);
  }
  const handleCopy = async () => {
    const textToCopy = copyRef.current.innerText;

    try {
      await navigator.clipboard.writeText(textToCopy);
      alert("a copy doanh thu");
    } catch (error) {
      console.log("copy tht bai");
    }
  };
  // xut ra excel
  const exportToExcel = () => {
    // To mt workbook v worksheet
    const wb = XLSX.utils.book_new();
    const wsData = [
      ...DataHandle.map((item, index) => ({
        STT: index + 1,
        "S in thoi": item.phone,
        "Tn Shop": item.brandName || "NULL",
        "S tin": item.amount,
        "% Tr Sau": Math.ceil(item.useMoneyPaylater / 1000000) + "%",
      })),
      {
        STT: "",
        "S in thoi": "",
        "Tn Shop": "Tng tin",
        "S tin": totalAmount,
        "% Tr Sau": "",
      },
      {
        STT: "",
        "S in thoi": "",
        "Tn Shop": "Ngy xut",
        "S tin": `${
          ActiveDay === "AllDay"
            ? `Toan b thang ${String(today.getMonth() + 1).padStart(2, "0")}`
            : ActiveDay === "AllDayThangTruoc"
            ? `Toan b thang ${
                String(today.getMonth() + 1).padStart(2, "0") - 1
              }`
            : `${day}-${month}-${year}`
        }`,
        "% Tr Sau": "",
      },
    ];
    const ws = XLSX.utils.json_to_sheet(wsData);

    // Thm worksheet vo workbook
    XLSX.utils.book_append_sheet(wb, ws, "D liu");

    // To file v ti xung
    XLSX.writeFile(
      wb,
      `QrTraSau${
        ActiveDay === "AllDay"
          ? `Toan b thang ${String(today.getMonth() + 1).padStart(2, "0")}`
          : ActiveDay === "AllDayThangTruoc"
          ? `Toan b thang ${String(today.getMonth() + 1).padStart(2, "0") - 1}`
          : `${day}-${month}-${year}`
      } .xlsx`
    );
  };


  return (
     
     <div className="container mx-auto p-4 bg-gray-200/50 relative ">
     <h1 className="text-2xl font-bold mb-4 text-center">
       Tng Tin Ngay:{" "}
       {ActiveDay === "AllDay"
         ? `Toan b thang ${String(today.getMonth() + 1).padStart(2, "0")}`
         : ActiveDay === "AllDayThangTruoc"
         ? `Toan b thang ${String(today.getMonth() + 1).padStart(2, "0") - 1}`
         : `${day}-${month}-${year}`}
     </h1>
     <div className="bg-[#ffb340] rounded-lg sticky top-0 h-12 sm:h-10 w-full flex items-center overflow-hidden font-semibold text-white text-[15px] mt-2 mb-2">
       <span className="scrolling-text">
          Nu Yu Mn Em. Mi ng Gp Xin Gi V 0362061339 Nguyn Vn Ngh MoMo. Thank All !!!
       </span>
     </div>

     <div className="flex justify-between">
       <button
         className={`block hover:bg-green-300/50 mb-5 px-2 py-1 rounded-lg ${
           ActiveDay === "Now" ? " bg-green-300" : ""
         }`}
         onClick={HandleTotalNow}
       >
         Tng Hm Nay:{" "}
         <span className="font-bold text-red-500">
           {String(today.getDate()).padStart(2, "0")}/
           {String(today.getMonth() + 1).padStart(2, "0")}
         </span>
       </button>
       <button
         className={`mb-5 px-2 py-1 rounded-lg border border-gray-500 hover:bg-pink-300/50`}
         onClick={HandleUse}
       >
         Hng Dn S Dung
       </button>
     </div>
     <div className="flex justify-between">
       <button
         className={`block hover:bg-green-300/50 mb-5 px-2 py-1 rounded-lg ${
           ActiveDay === "Yesterday" ? " bg-green-300" : ""
         }`}
         onClick={HandleTotalYesterday}
       >
         Tng Hm Qua :
         <span className="font-bold text-red-500">
           {today.getDate() === 1
             ? `${dayYesterday}/${monthYesterday}`
             : `${dayYesterday}/${month}`}
         </span>
       </button>
       <button
         className={`mb-5 px-2 py-1 rounded-lg animate-bounce bg-yellow-300 border border-gray-500`}
         onClick={() => {
           setisShow(true);
           setContent(<Donate />);
         }}
       >
         Donate me
       </button>
     </div>
     <button
       className={`block hover:bg-green-300/50 mb-5 px-2 py-1 rounded-lg ${
         ActiveDay === "AllDay" ? " bg-green-300" : ""
       }`}
       onClick={HandleTotalAllDayMonth}
     >
       Tng Thng Ny :
       <span className="font-bold text-red-500">{`${firstDay} to ${lastDay}`}</span>
     </button>
     <button
       className={`block hover:bg-green-300/50 mb-5 px-2 py-1 rounded-lg ${
         ActiveDay === "AllDayThangTruoc" ? " bg-green-300" : ""
       }`}
       onClick={HandleTotalAllDayMonthThangTruoc}
     >
       Tng Thng Trc :
       <span className="font-bold text-red-500">{`${firstDayx} to ${lastDayx}`}</span>
     </button>
     {/* <div className={`block hover:bg-green-300/50 mb-5 px-2 py-1 rounded-lg ${
         ActiveDay === "AllDaysSelect" ? " bg-green-300" : ""
       }`} onClick={HandleTotalSelectDay} >
         <h1>Chn khong thi gian</h1>
     
         <div>
       <label>Ngy bt u:</label>
       <DatePicker
         selected={parseDate(startDate)} // Chuyn chui ngy thnh i tng Date
         onChange={handleStartDateChange}
         selectsStart
        
         dateFormat="dd-MM-yyyy"
         placeholderText="Chn ngy bt u"
         locale="vi"
       />
     </div>

     <div>
       <label>Ngy kt thc:</label>
       <DatePicker
         selected={parseDate(endDate)} // Chuyn chui ngy thnh i tng Date
         onChange={handleEndDateChange}
         selectsEnd
        
         dateFormat="dd-MM-yyyy"
         placeholderText="Chn ngy kt thc"
         locale="vi"
         minDate={startDate ? parseDate(startDate) : null}
       />
     </div>

     <div>
       <p>Ngy bt u  chn: {startDate}</p>
       <p>Ngy kt thc  chn: {endDate}</p>
     </div>
         
     </div> */}

     <div className="flex items-center justify-center mb-4">
       <textarea
         value={newPhoneNumbers}
         onChange={(e) => setNewPhoneNumbers(e.target.value)}
         placeholder="Nhp dy s in thoi, ngn cch bng du cch hoc du phy"
         className="border border-gray-300 rounded p-2 mr-2 w-30"
         rows="4"
       />
       <div className="flex justify-center items-center mb-1">
         <input
           type="password"
           value={passwordT}
           onChange={(e) => setPasswordT(e.target.value)}
           placeholder="Pass Chung"
           className="border border-gray-300 rounded p-1 mr-2  w-[90%]"
         />
       </div>
     </div>

     <div className="flex items-center justify-center mb-3">
       <button
         onClick={handleAddPhoneNumbers}
         className="bg-blue-500 text-white px-7 py-2 rounded hover:bg-blue-600 font-medium"
       >
         Add ST
       </button>
     </div>
     {phoneNumbers.length > 0 && (
       <div className="flex items-center justify-center">
         <p>
           {" "}
           Co{" "}
           <span className="text-red-500 font-bold">
             {phoneNumbers.length}
           </span>{" "}
           st trong danh sach check
         </p>
       </div>
     )}
     <div className="flex items-center justify-center mt-1 mb-3">
       <button
         onClick={ClearData}
         className="bg-red-500 text-white px-6 py-2 rounded hover:bg-red-500/50 font-medium"
       >
         Clear Data
       </button>
     </div>
     <div className="flex items-center justify-center mb-3">
       {loading ? (
         <button className="flex bg-green-500 text-white px-6 py-2 rounded hover:bg-green-600 font-medium animate-pulse">
           <div className="w-6 h-6 border-2 border-black rounded-full border-t-white animate-spin "></div>
           <p className="px-3">ang X Li ....</p>
         </button>
       ) : (
         <button
           onClick={loginAllPhones}
           className="bg-green-500 text-white px-6 py-2 rounded hover:bg-green-600 font-medium"
         >
           CHECK QR
         </button>
       )}
     </div>
     <div className=" md:flex md:justify-between">
       <div className="flex gap-6 mb-1">
         <p
           className={`px-3 py-1 hover:bg-green-300/50  rounded-lg ${
             statusQR === "All" ? "bg-green-300 font-medium" : ""
           }`}
           onClick={QrHasBeenScannedAll}
         >
           All
         </p>
         <p
           className={`px-3 py-1 hover:bg-green-300/50  rounded-lg ${
             statusQR === "scan" ? "bg-green-300 font-medium" : ""
           }`}
           onClick={QrHasBeenScanned}
         >
           Ma a Quet
         </p>
         <p
           className={`px-3 py-1 hover:bg-green-300/50  rounded-lg ${
             statusQR === "notscan" ? "bg-green-300 font-medium" : ""
           }`}
           onClick={QrHasNotBeenScanned}
         >
           Ma Cha Quet
         </p>
         <p
           className={`px-3 py-1 hover:bg-green-300/50  rounded-lg ${
             statusQR === "pendingMoney" ? "bg-green-300 font-medium" : ""
           }`}
           onClick={QrPendingMoney}
         >
           Ma Treo Tin
         </p>
       </div>
       {isPhoneNumber(phoneNumbers[0]) ? (
         <div className="flex gap-3 items-center mb-1">
           <div className="flex gap-2">
             <div className=" rounded-lg w-6 h-6 bg-green-500" />{" "}
             <p>Vts Hoat ng</p>
           </div>
           <div className="flex gap-2">
             <div className=" rounded-lg w-6 h-6 bg-stone-500" />{" "}
             <p>Vts Tam Khoa</p>
           </div>
         </div>
       ) : (
         <p>
           <span className="text-red-600">Note:</span>Tai khoang nhn vin se
           khng check c vts khoa va % tra sau.
         </p>
       )}
     </div>
     <button
       onClick={exportToExcel}
       className="mt-4 bg-blue-500 text-white px-4 py-2 rounded"
     >
       Xut ra Excel
     </button>

     <div
       className="table-responsive"
       style={{ overflowX: "auto", WebkitOverflowScrolling: "touch" }}
     >
       <table className="w-full border-collapse border-[2px] border-gray-400 relative">
         <thead className=" sticky top-0 right-0">
           <tr className="bg-gray-200">
             <th className="border border-gray-300 px-1 py-1">STT</th>
             <th className="border border-gray-300 px-2 py-2">
               S in thoi
             </th>
             <th className="border border-gray-300 px-2 py-2">Tn Shop</th>
             <th className="border border-gray-300 px-2 py-2">S tin</th>
             <th className="border border-gray-300 px-2 py-2">% Tra Sau</th>
             <th className="border border-gray-300 px-2 py-2">Tin Treo</th>
             <th className="border border-gray-300 px-2 py-2">Kha VTS</th>
           </tr>
         </thead>
         <tbody className="max-h-96 overflow-y-auto">
           {DataHandle.length ? (
             DataHandle.map((item, index) => (
               <tr
                 key={item.phone}
                 className={`${
                   item.locked ? "bg-stone-500" : "bg-green-500"
                 } font-bold border-b hover:bg-white hover:text-red-500 text-center`}
               >
                 <td className="border border-gray-300 px-4 py-2">
                   {index + 1}
                 </td>
                 <td className="border border-gray-300 px-4 py-2">
                   {item.phone}
                 </td>
                 <td className="border border-gray-300 px-4 py-2">
                   {item.brandName || "NULL"}
                 </td>
                 <td className="border border-gray-300 px-4 py-2">
                   {item.amount.toLocaleString()} VND
                 </td>
                 <td className="border border-gray-300 px-4 py-2">
                   {Math.ceil(item.useMoneyPaylater / 1000000)} %
                 </td>
                 <td className="border border-gray-300 px-4 py-2">
                  <span className="text-red-600"> {item?.pendingAmount.toLocaleString()} VND</span>
                 </td>
                 <td className="border border-gray-300 px-4 py-2">
                  <span className="text-red-600 ">{item?.enabled ?"Cha Kha":" Kha"}</span>
                 </td>
               </tr>
             ))
           ) : (
             <tr>
               <td colSpan="5" className="text-center py-4">
                 Khng c d liu
               </td>
             </tr>
           )}
         </tbody>
       </table>
     </div>

     <div className="sticky bottom-0 w-full">
       {error ? (
         <h2 className="w-full text-xl font-bold text-center mt-6 text-red-600 bg-yellow-400">
           {error}
         </h2>
       ) : (
         <div className="bg-green-400 w-full p-5">
           {AllAmount.length === phoneNumbers.length &&
             phoneNumbers.length > 0 &&
             AllAmount.length > 0 && (
               <p className=" font-bold text-red-500 flex justify-center">
                 Check Xong Vo Lc {timeNow}
               </p>
             )}
           <h2
             ref={copyRef}
             onClick={handleCopy}
             className="text-xl font-bold text-center w-full"
           >
             Total{" "}
             {ActiveDay === "AllDay"
               ? `Toan b thang ${String(today.getMonth() + 1).padStart(
                   2,
                   "0"
                 )}`
               : ActiveDay === "AllDayThangTruoc"
               ? `Toan b thang ${
                   String(today.getMonth() + 1).padStart(2, "0") - 1
                 }`
               : `${day}-${month}-${year}`}
             :{" "}
             <span className="text-red-500">
               {" "}
               {totalAmount.toLocaleString()}{" "}
             </span>{" "}
             VND
           </h2>
         </div>
       )}
     </div>
     {error
       ? " "
       : AllAmount.length === phoneNumbers.length &&
         phoneNumbers.length > 0 &&
         AllAmount.length > 0 && (
           <div className="w-full mt-2">
             <p className="text-2xl font-bold text-center">Tinh Tin </p>
             <div className="container mx-auto p-4">
               <table className="table-auto w-full border-collapse  text-center">
                 <tbody>
                   <tr>
                     <td className="px-4 py-2 border">S tin gc</td>
                     <td className="px-4 py-2 border">
                       {totalAmount.toLocaleString()} VND
                     </td>
                   </tr>
                   <tr>
                     <td className="px-4 py-2 border">Phn trm gim gi</td>
                     <td className="px-4 py-2 border">
                       <select
                         className="border rounded  w-[70%] sm:w-[50%] md:w-[40%] lg:w-[30%]"
                         onChange={(e) => setpercent(e.target.value)}
                       >
                         {Array.from({ length: 19 }, (_, i) =>
                           ((i + 2) * 0.1).toFixed(1)
                         ).map((value) => (
                           <option
                             className="mx-auto"
                             key={value}
                             value={value}
                           >
                             {value} %
                           </option>
                         ))}
                       </select>
                     </td>
                   </tr>
                   <tr>
                     <td className="px-4 py-2 border"> S tin giam gia</td>
                     <td className="px-4 py-2 border">
                       {((totalAmount * percent) / 100).toLocaleString()} VND
                     </td>
                   </tr>
                   <tr className="bg-yellow-300">
                     <td className="px-4 py-2 border-b font-bold text-red-500">
                       S tin cui cung
                     </td>
                     <td className="px-4 py-2 border-b font-bold text-red-500">
                       {(
                         (totalAmount * (100 - percent)) /
                         100
                       ).toLocaleString()}{" "}
                       VND
                     </td>
                   </tr>
                 </tbody>
               </table>
             </div>
           </div>
         )}
   </div>)
   
}

export default App;
